@import "basic/fonts";
@import "basic/reset";
@import "basic/basic";
body {
  &.lock {
    overflow: hidden;
  }
  font-family: sans-serif;
}

.container {
  display: flex;
  justify-content: center;
  padding: 10px 0 50px;
  width: 100%;
}
.hidden {
  display: none;
}

.canvas {
  margin: 0 auto;
  height: 400px;
  background: url(../img/bg/bg.png) repeat;
  margin-bottom: 0;
  display: block;
  margin-bottom: 10px;
  &__wrapper {}
  &__tools {
    // outline: 1px solid red;
  }
}

label.disabled, label.disabled + input[type="radio"] {
  opacity: .5;
  &:hover {
    opacity: 1;
  }
}

.tools {
  display: flex;
  align-items: center;
  flex-direction: column;
  &__title {
    font-weight: 700;
    font-size: 20px;
    display: inline-block;
    margin-bottom: 5px;
  }
  &__row {
    margin-bottom: 8px;
  }
  &__column {
    margin-bottom: 8px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  & input {
    max-width: none;
  }
  & label {
    // display: flex;
    // flex-direction: column;
    // align-items: center;
  }
  &__slider-wrapper {
    // width: 100%;
    // overflow-x: hidden;
  }
  &__slider {
    overflow: hidden;
    background-color: rgb(158, 200, 255);
    width: 100%;
    margin-bottom: 10px;
    & > .swiper-wrapper {
      width: 100vw;
      height: 100%;

    }
  }
  &__slide {
    // padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 12px 8px;
    background-color: rgb(158, 200, 255);
  }
  &__text {
    width: 100%;
    color: rgb(0, 0, 0);
    padding: 6px 10px;
    margin-bottom: 10px;
    &::placeholder {
      color: rgba(0, 0, 0, 0.7);
    }
  }
  &__fontfamily {
    padding: 6px 10px;
    width: 100%;
    margin-bottom: 10px;
    & > option {
      padding: 6px 10px;
      display: inline-block;
    }
  }
  &__align-grid {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    align-content: flex-start;
    flex-wrap: wrap;
    padding-top: 10px;
    & input {
      flex: 0 0 30%;
      display: block;
      padding: 0;
      margin: 0;
      margin-bottom: 10px;
    }
  }
  &__save {
    display: block;
    background-color: rgb(158, 200, 255);
    padding: 15px;
    border-radius: 8px;
    &:focus {
      background-color: rgb(128, 177, 240);
    }
    &:active {
      background-color: rgb(111, 155, 212);
    }
    & > img {
      width: 30px;
      height: 30px;
      margin: 0 auto;
    }
  }
  &__textcolor {}
  &__strokewidth {}
  &__strokecolor {}
  &__fontsize {}
}
.panel {
  position: relative;
  z-index: 1;
  &.disabled {
    opacity: .4;
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}

.modal-addtext {
  &.visible {
    display: block;
  }
  &.hidden {
    display: none;
  }
  background: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  &__overlay {
    background: rgba(204, 204, 204, 0.562);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__content {
    // border: 1px solid red;
  }
  &__input {
    padding: 8px 10px;
    color: #000000;
    width: 100%;
    max-width: none;
    margin-bottom: 10px;
    &::placeholder {
      font-size: 1rem;
      line-height: 1.2;
      color: rgba(0, 0, 0, 0.7);
    }
  }
}
